import React from "react";
import { Grid } from "@mui/material";
import TypeWriterEffect from "react-typewriter-effect";

export default function TypedText({ isMobile }) {
  return (
    <>
      <Grid
        item
        xs={12}
        spacing={0}
        sx={{ ml: { xs: 3 } }}
        className={isMobile ? "lg-text-mobile mobie-ratio" : "lg-text-desktop"}
      >
        <item>
          <TypeWriterEffect
            startDelay={100}
            hideCursorAfterText={true}
            text="."
            typeSpeed={150}
          />
        </item>

        <item style={{ float: "left" }}>
          <TypeWriterEffect
            startDelay={1200}
            text=""
            hideCursorAfterText={true}
            typeSpeed={150}
            textStyle={{ color: "#FF6B6B" }}
          />
        </item>
        <item style={{ float: "none" }}>
          <TypeWriterEffect
            startDelay={1600}
            text=" "
            hideCursorAfterText={true}
            typeSpeed={150}
          />
        </item>

        <item>
          <TypeWriterEffect
            startDelay={2800}
            cursorColor="black"
            text="."
            typeSpeed={150}
            hideCursorAfterText={true}
          />
        </item>
        <item
          item
          spacing={0}
          xs={12}
          className={isMobile ? "sm-text-mobile" : "sm-text-desktop"}
        >
          <TypeWriterEffect
            startDelay={4000}
            cursorColor="black"
            text="# # #"
            typeSpeed={150}
          ></TypeWriterEffect>
        </item>
      </Grid>
    </>
  );
}
