import "./components/styles/IntroStyle.css";
import React from "react";
import { Grid } from "@mui/material";
import ActivitiesText from "./components/text/ActivitiesText";
import "./components/styles/Float.css";
import CardContent from "../components/CardComponent";

export default function Activities() {
  /*   */
  const actList = [
    {
      title: "  SW  (SSAFY) 6",
      contents:
        " SW     SW \n           ",
      date: "2021.07.07~ ",
    },
    {
      title: "SSAFY 6 1    ",
      contents:
        "           \n       ",
      date: "2021.11.18 - 2021.11.26 (3 )",
    },
    {
      title: "SSAFY 6 2    ",
      contents:
        "    ,     \n SNS  ",
      date: "2022.01.10 - 2022.02.18 (6 )",
    },
    {
      title: "2     ",
      contents: "      ",
      date: "2022.02.28 - 2022.04.08 (6 )",
    },
    {
      title: "SSAFY 6 ",
      contents: "1  3  & 2 // ",
      date: "2021.07.07~ ",
    },
    {
      title: "SSAFY Alumni 1",
      contents: "  SW     ()",
      date: "2022.03~",
    },
    {
      title: "Residence Assistance",
      contents:
        " residence hall  bennett tower ,   \n           ",
      date: "2018.01.01 ~ 2020.05.14 ",
    },
    {
      title: "Lane Department Tutor",
      contents:
        "WVU  CS/CPE/EE  ,     \n    CS      ",
      date: "2019.09.02 ~ 2020.05.14 ",
    },
  ];
  return (
    <div id="third">
      <div className="intro-container">
        {window.innerWidth < 600 ? (
          <Grid
            container
            direction="row"
            alignItems="center"
            sx={{ minHeight: "100vh" }}
          >
            <Grid
              item
              row
              container
              spacing={0}
              alignContent="center"
              justifyContent="center"
            >
              
            </Grid>
          </Grid>
        ) : (
          <Grid
            container
            direction="row"
            alignItems="center"
            style={{ minHeight: "100vh" }}
          >
            {/* Desktop : Text */}
            <Grid
              container
              direction="row"
              alignItems="end"
              justifyContent="end"
              sx={{ mt: -5 }}
            >
              <Grid direction="column" sx={{ mr: 10 }}>
                <ActivitiesText />
              </Grid>
            </Grid>
            {/*pink*/}
            <Grid
              className="activities-whiteBox"
              sx={{ ml: { sm: 10 }, mt: { sm: -20 } }}
            >
              {/* lights */}
              <Grid className="circle-red" />
              <Grid className="circle-yellow" />
              <Grid className="circle-green" />
              {/*white*/}
              <Grid
                className="activities-innerBox"
                sx={{ ml: { sm: 8 }, mt: { sm: 3 } }}
              ></Grid>
              <Grid
                container
                alignItems="center"
                justifyContent="center"
                direction="row"
                className="activities-table"
                alignContent="center"
              >
                {actList.map((act) => (
                  <CardContent key={act.title} activity={act} />
                ))}
              </Grid>
            </Grid>
          </Grid>
        )}
      </div>
    </div>
  );
}
