import React from "react";

import { styled } from "@mui/material/styles";
import { Card, Grid, CardActionArea } from "@mui/material";

//   
const StyledCard = styled(Card)(() => ({
  position: "relative",
  border: "none",
  outline: "none",
  color: "#666",
  "&:hover": {
    animation: "circlemove 1.5s infinite linear",
  },
  "@keyframes circlemove": {
    "0%,100%": { transform: "translate(-1%,-1%)" },
    "50%": {
      transform: "translate(-1%,-2%)",
      boxShadow: "rgba(0, 0, 0, 0.35) 0px 5px 15px",
    },
  },
}));

// 
const ProfileTitle = styled("div")(() => ({
  alignItems: "center",
  fontWeight: "bold",
  color: "black",
}));
//  
const ProfileDate = styled("div")(() => ({
  alignItems: "center",
  whiteSpace: "pre-line",
  color: "#FF6B6B",
}));
//  
const ProfileContent = styled("div")(() => ({
  alignItems: "center",
  color: "grey",
  whiteSpace: "pre-line",
}));

export default function CardComponent({ activity }) {
  const insideInfo = () => {
    return (
      <StyledCard
        variant="outlined"
        sx={{
          mx: activity.marginX,
          py: 3,
          px: 1,
        }}
      >
        <CardActionArea>
          <ProfileTitle
            sx={{ fontSize: { sm: "1.2rem", xs: "1rem", md: "1.5rem" } }}
          >
            {activity.title}
          </ProfileTitle>
          <ProfileDate
            sx={{ fontSize: { sm: "0.7rem", xs: "0.6rem", md: "0.8rem" } }}
          >
            {activity.date}
          </ProfileDate>
          <ProfileContent
            sx={{ fontSize: { sm: "1rem", xs: "0.8rem", md: "1.1rem" } }}
          >
            {activity.contents}
          </ProfileContent>
        </CardActionArea>
      </StyledCard>
    );
  };
  return (
    <Grid item xs={12} sm={6} md={6} lg={5}>
      {insideInfo()}
    </Grid>
  );
}
