import React from "react";
import LargeTypedModule from "../../../components/LargeTypedModule";
import LargeColorTypedModule from "../../../components/LargeColorTypedModule";
export default function InterestText() {
  return (
    <>
      <LargeTypedModule
        isMobile={false}
        msg={"..."}
        time={2000}
        inLine={true}
        lastWord={true}
      />
      <LargeColorTypedModule
        isMobile={false}
        msg={""}
        time={2600}
        inLine={true}
        lastWord={true}
      />
      <LargeTypedModule
        isMobile={false}
        msg={""}
        time={3100}
        inLine={true}
        lastWord={true}
      />
      <LargeColorTypedModule
        isMobile={false}
        inLine={true}
        msg={"  "}
        time={3250}
        lastWord={true}
      />
      <LargeTypedModule
        isMobile={false}
        msg={"    ."}
        time={5000}
        inLine={true}
        lastWord={false}
      />
    </>
  );
}
