import React from "react";
import "./ProjectCard.css";
/* Stylings */
import { styled } from "@mui/material/styles";
import { Card, Grid, CardActionArea, IconButton } from "@mui/material";
import AliceCarousel from "react-alice-carousel";
import "react-alice-carousel/lib/alice-carousel.css";
/* Icons */
import { ReactComponent as Notion } from "../assets/notion.svg";
import GitHubIcon from "@mui/icons-material/GitHub";
import PermContactCalendarRoundedIcon from "@mui/icons-material/PermContactCalendarRounded";
import Tooltip from "@mui/material/Tooltip";
//   
const StyledCard = styled(Card)(() => ({
  position: "relative",
  border: "none",
  outline: "none",
}));
// 
const ProjectTitle = styled("div")(() => ({
  alignItems: "center",
  fontWeight: "bold",
  color: "black",
}));
//  
const ProjectDate = styled("div")(() => ({
  alignItems: "center",
  whiteSpace: "pre-line",
  color: "#FF6B6B",
}));
//  
const ProjectSub = styled("div")(() => ({
  alignItems: "center",
  color: "grey",
  whiteSpace: "pre-line",
}));
//  
const ProjectContents = styled("p")(() => ({
  alignItems: "center",
  color: "black",
  whiteSpace: "pre-line",
}));

export default function ProjectCard({ projectInfo, imageInfo }) {
  return (
    <div className="whiteBox">
      <div className="circle-red" />
      <div className="circle-yellow" />
      <div className="circle-green" />
      <div className="innerBox">
        <Grid container justify="flex-start">
          <Grid item xs={7} sx={{ pr: 0, pt: 2 }}>
            {/* Carousel   */}
            <AliceCarousel controlsStrategy="responsive" items={imageInfo} />
          </Grid>
          <Grid item xs={5}>
            {/* github */}
            <Tooltip title="   " placement="top">
              <IconButton
                aria-label="github"
                size="large"
                href={projectInfo.github}
                style={{ pointerEvents: "auto" }}
              >
                <GitHubIcon fontSize="large" style={{ color: "black" }} />
              </IconButton>
            </Tooltip>
            {/* Notion */}
            <Tooltip title="   " placement="top">
              <IconButton
                aria-label="notion"
                size="small"
                href={projectInfo.notion}
                style={{ pointerEvents: "auto" }}
              >
                <Notion fontSize="large" />
              </IconButton>
            </Tooltip>
            {/* Team  */}
            <Tooltip title="   " placement="top">
              <IconButton
                aria-label="notion"
                size="large"
                href={projectInfo.team}
                style={{ pointerEvents: "auto" }}
              >
                <PermContactCalendarRoundedIcon
                  fontSize="large"
                  style={{ color: "black" }}
                />
              </IconButton>
            </Tooltip>

            {/*     */}
            <StyledCard variant="outlined" sx={{ pr: 2 }}>
              <ProjectTitle
                sx={{
                  fontSize: { sm: "2rem", xs: "1rem", md: "2rem" },
                  mt: 6,
                }}
              >
                {projectInfo.title}
              </ProjectTitle>
              <CardActionArea style={{ pointerEvents: "none" }}>
                <ProjectDate
                  sx={{
                    fontSize: {
                      sm: "1rem",
                      xs: "0.8em",
                      md: "1rem",
                    },
                  }}
                >
                  {projectInfo.date}
                </ProjectDate>
                <ProjectSub
                  sx={{
                    fontSize: { sm: "1rem", xs: "0.8em", md: "1rem" },
                    mt: 1.5,
                  }}
                >
                  {projectInfo.summary}
                </ProjectSub>

                <ProjectSub
                  sx={{
                    fontSize: { sm: "1rem", xs: "0.8em", md: "1rem" },
                    color: "black",
                    my: 3,
                  }}
                >
                   / 
                </ProjectSub>
                <ProjectContents
                  sx={{
                    fontSize: { sm: "1rem", xs: "0.8em", md: "1rem" },
                  }}
                >
                  {projectInfo.learned}
                </ProjectContents>
              </CardActionArea>
            </StyledCard>
          </Grid>
        </Grid>
      </div>
      {/*         */}
    </div>
  );
}
