import "./components/styles/IntroStyle.css";
import React from "react";
import { Grid, Box, Typography } from "@mui/material";
import InterestText from "./components/text/InterestText";
import SkillStacks from "./components/TagCloud";
import "./components/styles/Float.css";

export default function Interests() {
  const MobileImg = require("../assets/mobile.jpg");
  const DesktopImg = require("../assets/desktop.jpg");
  return (
    <div id="second">
      <div className="intro-container">
        {window.innerWidth < 600 ? (
          <Grid
            container
            direction="row"
            alignItems="center"
            sx={{ minHeight: "100vh" }}
          >
            <Grid
              item
              row
              container
              spacing={0}
              alignContent="center"
              justifyContent="center"
            >
              
            </Grid>
          </Grid>
        ) : (
          <Grid
            container
            direction="row"
            alignItems="center"
            style={{ minHeight: "100vh" }}
          >
            {/* Desktop : Text */}
            <Grid container direction="row" alignItems="center" sx={{ mt: -5 }}>
              <Grid direction="column">
                <InterestText />
              </Grid>
            </Grid>
            <Grid container direction="row" sx={{ mt: -30 }}>
              <Grid
                container
                direction="column"
                item
                xs={3}
                sx={{ ml: 30, mr: -30 }}
              >
                <Box>
                  <img
                    className="intro-aboutMe-desktop  "
                    style={{ maxWidth: "40vw" }}
                    src={DesktopImg}
                    alt="DesktopImg"
                  />
                </Box>
                <Typography>
                       
                      .
                </Typography>
              </Grid>
              <Grid container direction="column" item xs={3}>
                <Box>
                  <img
                    className="intro-aboutMe-desktop floating"
                    style={{
                      maxWidth: "12vw",
                      position: "absolute",
                      left: "100px",
                    }}
                    src={MobileImg}
                    alt="MobileImg"
                  />
                </Box>
              </Grid>

              <Grid
                container
                direction="column"
                alignItems="center"
                item
                xs={5}
                sx={{ pl: 10, pt: 5 }}
              >
                <SkillStacks />
              </Grid>
            </Grid>
          </Grid>
        )}
      </div>
    </div>
  );
}
